import { NgModule }       from '@angular/core';
import { CommonModule } from '@angular/common';
import { DialogComponent } from './dialog.component';
import { DialogTitleBarComponent } from './dialog-titlebar.component';
import { DialogActionsComponent } from './dialog-actions.component';
/**
 * Export for all the individual components.
 */
export const DIALOG_DIRECTIVES: any[] = [
    DialogComponent,
    DialogTitleBarComponent,
    DialogActionsComponent
];

/**
 * The exported package module
 */
@NgModule({
    declarations: [DIALOG_DIRECTIVES],
    exports: [DIALOG_DIRECTIVES],
    imports: [CommonModule]
})
export class DialogModule {}



